import {
  Box, Button, ButtonGroup, Divider
} from '@material-ui/core'
import React, { useState } from 'react'
import {
  Datagrid,
  DateField, EditButton, FunctionField, GET_LIST, List, Pagination, Record, ResourceContextProvider, sanitizeFetchType, Show,
  ShowProps, Tab, TabbedShowLayout, TextField, TextInput,
  TitleProps, TopToolbar, useListContext, useMutation, useNotify, useRefresh, useShowContext, useTranslate
} from 'react-admin'
import { EngineDto } from '../../../core/dto/device/engine/engine.dto'
import { EntranceDto } from '../../../core/dto/entrance.dto'
import { EventDto } from '../../../core/dto/event/event.dto'
import { ResourceName } from '../../../core/ResourceName'
import { CheckboxField } from '../../common/CheckboxField'
import { ConfirmDialog, LockModal, SetStandbyModeModal, SetTSCanCredentialsModal } from './EngineDialogs'
import { useHasAuthority } from '../../hooks/useHasAuthority'
import { Authority } from '../../../core/auth/Authority'

interface EngineTitleProps extends TitleProps {
  record?: EngineDto
}

const ShowTitle = (data: EngineTitleProps) => {
  const {
    record: engine,
  } = data
  const translate = useTranslate()
  return (
    <span>
      {translate('resources.engines.titles.show')}: {engine?.id}. {engine?.name}
    </span>
  )
}

const GeneralActions = () => {
  const { record: engine } = useShowContext<EngineDto>()
  const [lockModalOpen, setLockModalOpen] = useState(false)
  const [unlockDialogOpen, setUnlockDialogOpen] = useState(false)
  const [standbyModeModalOpen, setStandbyModeModalOpen] = useState(false)
  const [removeTSCanCredentialsDialogOpen, setRemoveTSCanCredentialsDialogOpen] = useState(false)
  const [tsCanCredentialsModalOpen, setTSCanCredentialsModalOpen] = useState(false)
  const [mutate] = useMutation()
  const refresh = useRefresh()
  const translate = useTranslate()

  const handleSetTSCanCredentialsDialog = () => {
    setTSCanCredentialsModalOpen(true)
  }

  const handleRemoveTSCanCredentials = async () => {
    setRemoveTSCanCredentialsDialogOpen(false)
    await mutate(
      {
        type: sanitizeFetchType('removeTSCanCredentials'),
        resource: ResourceName.ENGINES,
        payload: {
          engineId: engine?.id,
        },
      },
      { returnPromise: true },
    )
    refresh()
  }

  const handleRemoveTSCanCredentialsDialog = () => {
    setRemoveTSCanCredentialsDialogOpen(true)
  }

  const handleSetStandbyModeDialog = () => {
    setStandbyModeModalOpen(true)
  }

  const handleLock = async () => {
    setUnlockDialogOpen(false)
    await mutate(
      {
        type: sanitizeFetchType('unlock'),
        resource: ResourceName.ENGINES,
        payload: {
          engineId: engine?.id,
        },
      },
      { returnPromise: true },
    )
    refresh()
  }

  const handleLockDialog = () => {
    if (engine?.disabled) setUnlockDialogOpen(true)
    else setLockModalOpen(true)
  }

  return (
    <>
      <Divider />
      <SetTSCanCredentialsModal
        open={tsCanCredentialsModalOpen}
        close={() => setTSCanCredentialsModalOpen(false)}
      />
      <SetStandbyModeModal
        open={standbyModeModalOpen}
        close={() => setStandbyModeModalOpen(false)}
      />
      <LockModal open={lockModalOpen} close={() => setLockModalOpen(false)} />
      <ConfirmDialog
        open={unlockDialogOpen}
        confirm={handleLock}
        source="lock"
        cancel={() => setUnlockDialogOpen(false)}
        action="remove"
      />
      <ConfirmDialog
        open={removeTSCanCredentialsDialogOpen}
        confirm={handleRemoveTSCanCredentials}
        source="setTSCanCredentials"
        cancel={() => setRemoveTSCanCredentialsDialogOpen(false)}
        action="remove"
      />
      <Box p={1} display="block" textAlign="right">
        <Button
          variant="contained"
          onClick={handleSetTSCanCredentialsDialog}
          color="primary"
          style={{ margin: '2px' }}
        >
          {translate('resources.engines.actions.setTSCanCredentials')}
        </Button>
        <Button
          variant="contained"
          onClick={handleRemoveTSCanCredentialsDialog}
          color="primary"
          style={{ margin: '2px' }}
        >
          {translate('resources.engines.actions.removeTSCanCredentials')}
        </Button>
        <Button
          variant="contained"
          onClick={handleSetStandbyModeDialog}
          color="primary"
          style={{ margin: '2px' }}
        >
          {translate('resources.engines.actions.setStandbyMode')}
        </Button>
        <Button
          variant="contained"
          onClick={handleLockDialog}
          color="primary"
          style={{ margin: '2px' }}
        >
          {engine?.disabled
            ? translate('resources.engines.actions.unlock')
            : translate('resources.engines.actions.lock')}
        </Button>
        <EditButton basePath="/engines" record={engine} variant="contained" style={{ margin: '2px' }} size='medium' />
      </Box>
    </>
  )
}

const RelationTab = ({ source, mode }: { source: ResourceName.EVENTS | ResourceName.ENTRANCES, mode: 'show' | 'edit' }) => {
  const translate = useTranslate()
  const [mutate] = useMutation()
  const notify = useNotify()
  const hasAuthority = useHasAuthority()
  const { record: engine, basePath, refetch } = useShowContext<EngineDto>()

  const handleAttach = async (idsToAttach: number[]) => {
    switch (source) {
      case ResourceName.EVENTS:
        await mutate(
          {
            type: sanitizeFetchType('attachEvents'),
            resource: ResourceName.ENGINES,
            payload: {
              engineId: engine?.id,
              eventsIds: idsToAttach,
            },
          },
          { returnPromise: true },
        )
        break
      case ResourceName.ENTRANCES:
        await mutate(
          {
            type: sanitizeFetchType('attachEntrances'),
            resource: ResourceName.ENGINES,
            payload: {
              engineId: engine?.id,
              entrancesIds: idsToAttach,
            },
          },
          { returnPromise: true },
        )
        break
      default:
        notify(`Source '${source}' is not implemented yet.`)
    }
    if (refetch) {
      refetch()
    }
  }

  const handleDetach = async (idsToDetach: number[]) => {
    switch (source) {
      case ResourceName.EVENTS:
        await mutate(
          {
            type: sanitizeFetchType('detachEvents'),
            resource: ResourceName.ENGINES,
            payload: {
              engineId: engine?.id,
              eventsIds: idsToDetach,
            },
          },
          { returnPromise: true },
        )
        break
      case ResourceName.ENTRANCES:
        await mutate(
          {
            type: sanitizeFetchType('detachEntrances'),
            resource: ResourceName.ENGINES,
            payload: {
              engineId: engine?.id,
              entrancesIds: idsToDetach,
            },
          },
          { returnPromise: true },
        )
        break
      default:
        notify(`Source '${source}' is not implemented yet.`)
    }
    if (refetch) {
      refetch()
    }
  }

  const isAttached = (relatedRecordId: number) => {
    switch (source) {
      case ResourceName.EVENTS:
        return engine?.eventsIds?.includes(relatedRecordId)
      case ResourceName.ENTRANCES:
        return engine?.entrancesIds?.includes(relatedRecordId)
      default:
        notify(`Source '${source}' is not implemented yet.`)
        return false
    }
  }

  const listFilters = () => {
    switch (source) {
      case ResourceName.EVENTS:
        return ([<TextInput source="name" alwaysOn />])
      case ResourceName.ENTRANCES:
        return ([<TextInput source="note" alwaysOn />])
      default: return (undefined)
    }
  }

  const listSort = () => {
    switch (source) {
      case ResourceName.EVENTS:
        return ({ field: 'dateStart', order: 'DESC' })
      case ResourceName.ENTRANCES:
        return ({ field: 'note', order: 'ASC' })
      default: return (undefined)
    }
  }

  const listColumns = () => {
    switch (source) {
      case ResourceName.EVENTS:
        return (<TextField source="name" />)
      case ResourceName.ENTRANCES:
        return (<TextField source="note" />)
      default: return (undefined)
    }
  }

  const Actions = () => {
    const { filterValues } = useListContext<EventDto | EntranceDto>()
    const getFilteredRelatedRecordsIds = async () =>
      (await mutate(
        {
          type: sanitizeFetchType(GET_LIST),
          resource: source,
          payload: {
            filter: {
              ...filterValues
            }
          }
        },
        { returnPromise: true }
      ))?.data?.map((record: EventDto | EntranceDto) => record?.id)

    const handleAttachAll = async () => {
      handleAttach(await getFilteredRelatedRecordsIds())
    }

    const handleDetachAll = async () => {
      handleDetach(await getFilteredRelatedRecordsIds())
    }

    return (
      <TopToolbar>
        <ButtonGroup>
          <Button
            variant="contained"
            disabled={source === ResourceName.EVENTS && !hasAuthority(Authority.EDIT_EVENTS)}
            onClick={() => handleAttachAll()}
          >
            {translate('common.related-records.actions.attach-all')}
          </Button>
          <Button
            variant="contained"
            disabled={source === ResourceName.EVENTS && !hasAuthority(Authority.EDIT_EVENTS)}
            onClick={() => handleDetachAll()}
          >
            {translate('common.related-records.actions.detach-all')}
          </Button>
        </ButtonGroup>
      </TopToolbar>
    )
  }

  return (
    <ResourceContextProvider value={source}>
      <List
        basePath={basePath}
        exporter={false}
        title=" "
        filters={listFilters()}
        sort={listSort()}
        pagination={<Pagination rowsPerPageOptions={[10, 20, 50, 100]} />}
        perPage={20}
        actions={mode === 'edit' ? <Actions /> : <></>}
      >
        <Datagrid size="small" style={{ tableLayout: 'fixed' }} >
          {listColumns()}
          <CheckboxField
            disabled={mode !== 'edit'}
            sortable={false}
            checkBySource={(id: number) => isAttached(id)}
            source="id"
            label={translate(`common.related-records.fields.is-attached`)}
            onClick={
              (id: number) => isAttached(id as number)
                ? () => handleDetach([id as number])
                : () => handleAttach([id as number])
            } />
        </Datagrid>
      </List>
    </ResourceContextProvider>
  )
}

const EngineTabs = () => {
  const translate = useTranslate()
  const { record: engine } = useShowContext<EngineDto>()
  const hasAuthority = useHasAuthority()

  return (
    <TabbedShowLayout>
      <Tab label={translate('resources.engines.tabs.general')}>
        <TextField label="ID" source="id" />
        <TextField label="resources.engines.fields.name" source="name" />
        <FunctionField
          label="resources.engines.fields.isWatched"
          source="isWatched"
          render={(record: Record | undefined) =>
            record?.isWatched ? translate('const.yes') : translate('const.no')
          }
        />
        <DateField
          label="resources.engines.fields.lastRefresh"
          source="lastRefresh"
          showTime
          options={{ day: "2-digit", month: "2-digit", year: "numeric", hour: "2-digit", minute: "2-digit" }}
        />
        <TextField
          label="resources.engines.fields.address"
          source="address"
        />
        <FunctionField
          label="resources.engines.fields.direction"
          source="direction"
          render={(record: Record | undefined) => translate(`resources.enums.deviceDirection.${record?.direction}`)}
        />
        <TextField
          label="resources.engines.fields.categoryId"
          source="categoryId"
        />
        {engine?.disabled && (
          <TextField
            label="resources.engines.fields.disabled"
            source="disabled"
          />
        )}
        <FunctionField
          label="resources.engines.fields.stanbyMode"
          source="stanbyMode"
          render={(record: Record | undefined) => translate(`resources.enums.standbyMode.${record?.stanbyMode}`)}
        />
        <FunctionField
          label="resources.engines.fields.onlineInfixCeck"
          source="onlineInfixCeck"
          render={(record: Record | undefined) =>
            record?.onlineInfixCeck
              ? translate('const.yes')
              : translate('const.no')
          }
        />
        <FunctionField
          label="resources.engines.fields.multiplicity"
          source="multiplicity"
          render={(record: Record | undefined) => translate(`resources.enums.multiplicities.${record?.multiplicity}`)}
        />
        <GeneralActions />
      </Tab>
      {hasAuthority(Authority.VIEW_EVENTS) && (
        <Tab label={translate('resources.engines.tabs.events')}>
          <RelationTab source={ResourceName.EVENTS} mode={hasAuthority(Authority.EDIT_EVENTS) ? 'edit' : 'show'} />
        </Tab>
      )}
      <Tab label={translate('resources.engines.tabs.entrances')}>
        <RelationTab source={ResourceName.ENTRANCES} mode='edit' />
      </Tab>
    </TabbedShowLayout>
  )
}

export const EngineShow = (props: ShowProps) => (
  <Show {...props} actions={false} title={<ShowTitle />}>
    <EngineTabs />
  </Show>
)