import { DataProvider, GetListParams, GetListResult } from 'react-admin'
import { ENTRANCES_URL } from '../api-urls'
import { getWithPagination } from '../common/fetch.utils'
import { ALL_RESULTS } from '../common/filter.values'
import { EntranceDto } from '../dto/entrance.dto'
import { mapSortEntranceParam } from './entrance-sort.mapper'

interface EntranceListParams extends GetListParams {
  readonly sort: {
    readonly field: keyof EntranceDto
    readonly order: 'ASC' | 'DESC'
  }
  readonly filter:
  {
    readonly note: string
  }
}

const provider = {
  getList: async (
    resource: string,
    {
      filter: { note },
      sort,
      pagination
    }: EntranceListParams,
  ): Promise<GetListResult<EntranceDto>> => {

    const queryParams: string[] = []
    if (sort) {
      const { field, order } = sort
      queryParams.push(`sortBy=${mapSortEntranceParam(field)}`)
      queryParams.push(`sortOrder=${order}`)
    }
    if (pagination) {
      const { page, perPage } = pagination
      queryParams.push(`rangeFrom=${page * perPage - perPage}`)
      queryParams.push(`rangeTo=${page * perPage}`)
    }
    const pathParams = queryParams.length > 0 ? queryParams.reduce((p, c) => `${p}&${c}`) : null
    const { data, range: { total } } = await getWithPagination<EntranceDto[]>(`${ENTRANCES_URL}/GetByConditions/`, `${note ? `e=>e.Note.Contains("${note}")` : ALL_RESULTS}?${pathParams ?? pathParams}`)

    return Promise.resolve({
      data,
      total
    })
  }
} as DataProvider

export default provider